.health-card {
    width: 100%;
    height: 200px;
    border-radius: var(--border-radius-2);
    background: var(--main-bg-color-2);
    display: flex;
    flex-direction: row;
}

.health-card-left {
    width: 200px;
    height: 100%;
}

.left-head {
    width: calc(100% - 15px);
    height: calc(15% - 10px);
    padding-top: 10px;
    padding-left: 15px;
    display: flex;
    align-items: center;
    font-size: var(--font-size-small);
    font-weight: bold;
    color: var(--primary-color-1);
}

.left-content {
    width: 100%;
    height: 70%;
    font-weight: bold;
}

.left-bottom {
    width: 100%;
    height: 15%;
}

.health-card-right {
    width: calc(100% - 200px);
    height: 100%;
}


.right-head {
    width: calc(100% - 15px);
    height: calc(15% - 10px);
    padding-top: 10px;
    padding-right: 15px;
    display: flex;
    align-items: center;
    justify-content: end;
    font-size: var(--font-size-mini);
    color: var(--primary-color-1);
    font-weight: bold;
}

.right-head-value {
    color: var(--primary-color-2);
}

.right-content {
    width: 100%;
    height: 85%;
    font-weight: bold;
    display: flex;
    flex-direction: row;
    gap: 10px;
    overflow: auto;
}

.todo-item {
    flex-shrink: 0;
    height: calc(100% - 20px);
    margin: 10px 0 10px 0;
    width: 60px;
    background: var(--primary-color-1);
    border-radius: var(--border-radius-2);
    color: white;
}

.todo-item-title {
    width: 100%;
    height: 65%;
    text-align: center;
    font-size: var(--font-size-small);
    font-weight: bold;
}

.todo-item-time {
    width: 100%;
    height: 15%;
    text-align: center;
    font-size: var(--font-size-mini);
}

.todo-item-btn {
    width: calc(100% - 6px);
    height: calc(20% - 6px);
    font-size: var(--font-size-mini);
    background: var(--primary-color-2);
    margin: 3px;
    text-align: center;
    border-radius: var(--border-radius-2);
    cursor: pointer;
}

.done-list {
    height: calc(100% - 20px);
    width: calc(100% - 20px);
    padding: 10px;
    overflow: auto;
}

.done-item {
    width: calc(100% - 20px);
    height: 50px;
    padding: 10px;
    background: var(--main-bg-color-1);
    display: flex;
    flex-direction: row;
}

.done-info {
    width: calc(100% - 50px);
    display: flex;
    align-items: center;
    color: var(--main-text-color-2);
    font-size: var(--font-size-mid);
}

.done-btn {
    height: 100%;
    width: 50px;
    font-weight: bold;
    color: var(--primary-color-3);
}