.x-data {
    position: absolute;
    left: 0;
    right: 0;
    top: 55px;
    bottom: 0;
    overflow: auto;
}

.x-data-type {
    width: 100%;
}

.type-title {
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    font-size: var(--font-size-small);
}

.type-content {
    width: calc(100% - 20px);
    padding: 0 10px 0 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}