.system-env {
    height: 55px;
    width: calc(100% - 20px);
    display: flex;
    padding: 0 10px 0 10px;
}

.system-env-hello {
    width: 50%;
    display: flex;
    align-items: center;
}

.hello-text {
    font-size: var(--font-size-big);
    font-weight: bold;
}

.system-env-info {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: end;
    font-size: var(--font-size-small);
}

.env-info-left {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.weather-icon {
    height: 50%;
    font-size: var(--font-size-mid);
}

.weather-text {
    height: 50%;
}

.env-info-right {
    height: 100%;
    width: 80px;
    display: flex;
    flex-direction: column;
}

.year-month {
    width: 100%;
    height: 50%;
    color: var(--main-text-color-2);
}

.date-day {
    width: 100%;
    height: 50%;
    display: flex;
    font-weight: bold;
}

.date-day-date {
    height: 100%;
    width: 50%;
}

.date-day-day {
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: var(--primary-color-2);
}